import constants from '../constants';
import Utils from '../utils'

export default {

  html5Attributes: {
    message: 'message',
    min: 'min',
    max: 'max',
    trim: 'trim',
    utf8bytes: 'utf8Bytes',
  },

  enableByHtml5: function ($field) {

    let options = {};
    let maxLength = $field.attr('maxlength');
    let minLength = $field.attr('minlength');

    if (maxLength) {
      options.max = parseInt(maxLength, 10)
    }

    if (minLength) {
      options.min = parseInt(minLength, 10)
    }

    return $.isEmptyObject(options) ? false : options
  },

  /**
   * æ£æ¥åç´ å¼çé¿åº¦æ¯å¦å°äºæå¤§äºç»å®çæ°å­
   *
   * @param {bootstrapValidation} validation éªè¯å¨æä»¶å®ä¾
   * @param {jQuery} $field å­æ®µåç´ 
   * @param {Object} options ç±ä»¥ä¸é®ç»æ:
   * - min
   * - max
   *      - æ°å­
   *      - å­æ®µçåç§°ï¼å¶å¼å®ä¹æ°å­
   *      - è¿åæ°å­çåè°å½æ°çåç§°
   *      - è¿åæ°å­çåè°å½æ°
   *
   * - message: æ ææ¶æ¯
   * - trim: æç¤ºæ¯å¦å¨ä¿®åªå¼åè®¡ç®é¿åº¦ãé»è®¤æåµä¸ä¸ºfalse
   * - utf8bytes: ä»¥UTF-8å­èè®¡ç®å­ç¬¦ä¸²é¿åº¦ï¼é»è®¤ä¸ºfalse
   * @returns {Object}
   */
  rule: function (validation, $field, options) {

    let value = $field.val()

    // console.log(validation, $field, options);

    if (options.trim === true || options.trim === 'true') {
      value = $.trim(value)
    }

    if (value === '') {
      return true
    }

    let min = $.isNumeric(options.min)
      ? options.min
      : validation.getDynamicOption($field, options.min);


    let max = $.isNumeric(options.max)
      ? options.max
      : validation.getDynamicOption($field, options.max);



    let isValid = true;
    let length = options.utf8Bytes ? Utils.utf8Length(value) : value.length;
    let message = options.message || $.fn[constants.NAME].i18n.stringLength['default']

    if (
      (min && length < parseInt(min, 10)) ||
      (max && length > parseInt(max, 10))
    ) {
      isValid = false
    }

    switch (true) {
      case !!min && !!max:
        message = Utils.format(
          options.message || $.fn[constants.NAME].i18n.stringLength.between,
          [parseInt(min, 10), parseInt(max, 10)],
        )
        break

      case !!min:
        message = Utils.format(
          options.message || $.fn[constants.NAME].i18n.stringLength.more,
          parseInt(min, 10),
        )
        break

      case !!max:
        message = Utils.format(
          options.message || $.fn[constants.NAME].i18n.stringLength.less,
          parseInt(max, 10),
        )
        break

      default:
        break
    }

    return { valid: isValid, message: message }
  },
}
